import request from '../../util/request'

export default {
    name: 'Search',
    data () {
        return {
            searchText: '',
            tabCur: 'native',
            searchHistory: [],
            fastResult: [],
            debounceFlag: true,
            webTabs: [
                {
                    key: 'native',
                    value: '本站搜索'
                },
                {
                    key: 'ecdemic',
                    value: '全网搜索'
                }
            ],
            tipInfo: {
                'native': '搜索已录入在本站的书籍，搜索更快',
                'ecdemic': '海量全网搜索(自定义资源路径)'
            },
            sourceModal: false,
            sourceBox: [
                {
                    value: 'biquge',
                    name: '笔趣阁',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'qidian',
                    name: '起点',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'chuangshi',
                    name: '创世',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'meiwen',
                    name: '美文',
                    checked: false,
                    hot: true,
                }
            ]
        }
    },
    onLoad () {
        this.searchHistory = uni.getStorageSync('searchHistory') || []
    },
    methods: {
        tabSelect (e) {
            this.tabCur = e.currentTarget.dataset.web
        },
        inputBtn (e) {
            if (this.tabCur === 'native' && this.debounceFlag) {
                this.debounceFlag = false;
                setTimeout(() => {
                    this.fastQueryBooks(e.detail.value);
                }, 1000)
            }
        },
        confirmBtn (e) {
            this.searchHistory.push(e.detail.value)
            uni.setStorageSync('searchHistory', this.searchHistory)
            console.error(2, e.detail.value)
        },
        fastQueryBooks (authorOrTitle) {
            if (authorOrTitle) {
                let params = {
                    authorOrTitle: authorOrTitle
                }
                request.get('/novels/fastSearch', params).then(data => {
                    if (data.status === 200 && data.data.length > 0) {
                        this.fastResult = data.data
                    }
                }).finally(() => {
                    if (!this.debounceFlag) {
                        this.debounceFlag = true;
                    }
                })
            }
        },
        queryBooksBtn () {
            uni.showLoading({ title: 'loading...', mask: true })
            /// let params
        },
        clearBtn () {
            this.searchText = ''
            this.fastResult = []
        },
        hideSourceModal () {
            this.sourceModal = false
        },
        openSourceModal () {
            this.sourceModal = true
        },
        ChooseCheckbox (e) {
            let items = this.sourceBox
            let current = e.currentTarget.dataset.value
            for (let i = 0, lenI = items.length; i < lenI; ++i) {
                if (items[i].value === current) {
                    if (items[i].checked && this.sourceBox.filter(item => item.checked).length === 1) {
                        return
                    }
                    items[i].checked = !items[i].checked
                    break
                }
            }
        },
        deleteSearchHistoryBtn () {
            this.searchHistory = []
            uni.removeStorageSync('searchHistory')
        },
    }
}
